@media only screen and (min-width: 1551px) { 


    .hostingView {
        width: 100%;
        height: 100vh;
        overflow: hidden;
    }
    
    .hostingView_top {
        width: 100%;
        height: 10%;
        background-color: #2e1e12;
        display: flex;
        justify-content: flex-end;
        align-items: center;
        position: relative;
    }
    
    .hostView_startBtn {
        width: 110px;
        height: 40px;
        background-color: #22b703;
        border-radius: 7px;
        font-family: 'Luckiest Guy', cursive;
        color: white;
        font-size: 15px;
        margin-right: 60px;
        border: none;
        cursor: pointer;
        position: absolute;
    }
    
    .hostingView_middle {
        width: 100%;
        height: 30%;
        background-color: #6d594c;
        display: flex;
        justify-content: center;
        align-items: center;
        flex-direction: column;
    }
    
    .joinDesc {
        font-size: 30px;
        color: white;
        font-weight: bold;
        margin: 0;
        margin-bottom: -20px;
    }
    
    .joinCode {
        font-size: 120px;
        color: white;
        font-weight: bold;
        margin: 0;
    }
    
    .hostingView_bottom {
        width: 100%;
        height: 60%;
        background-color: #90715d;
     
        display: inline-flex;
      flex-wrap: wrap;
        justify-content: center;
        align-content: flex-start;
    }
    
    .playerTags {
        width: auto;
        height: 40px;
        background-color: #2e1e12;
        border-radius: 7px;
        color: white;
        font-size: 15px;
        font-weight: bold;
        border: none;
        cursor: pointer;
        display: flex;
        justify-content: center;
        align-items: center;
        margin-top: 20px;
        margin-left: 20px;
        padding-left: 20px;
        padding-right: 20px;
    }
    
    .playerTags:hover {
        background-color: #4d4d4d;
        text-decoration: line-through;
        text-decoration-thickness: 2px;
    }





}

